.main-portrait {
	width: 100vw;

	&-title {
		position: absolute;
		width: 100%;
		top: 10vh;

		@include mq(lg) {
			top: 18vh;
		}

		& h1 {
			font-size: 46px;
			text-align: center;
			@include mq(sm) {
				font-size: 70px;
			}

			@include mq(lg) {
				font-size: 124px;
			}
			& span {
				font-size: 20px;
			}
		}
	}

	&-bg {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100vh;
		clip-path: polygon(10vw 19vh, 90vw 19vh, 90vw 85vh, 10vw 85vh);

		@include mq(sm) {
			clip-path: polygon(10vw 22vh, 90vw 22vh, 90vw 100vh, 10vw 100vh);
		}

		& img {
			position: absolute;
			top: 15vh;
			height: 100%;
			//transform: translateX(-50%) scale(0.8);
			left: 50%;
			transform: translateX(-50%);
			filter: sepia(73%) grayscale(46%);

			@include mq(lg) {
				top: 36vh;
				width: 100vw;
				left: 0;
				transform: translateX(0%);
				height: auto;
			}
		}
	}
}
