.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	@include flex(row, center, space-between);
	padding: 15px 20px;

	@include mq(lg) {
		margin-block: 60px;
		padding-inline: 45px;
	}

	&-lang {
		&-en {
			text-transform: capitalize;
			border: none;
			background: none;
			color: $clr-black;
			font-weight: $fw-bolder;
			padding: 0px 5px 0px 0px;
		}

		&-es {
			@extend .header-lang-en;
			color: $clr-gray;
		}
	}

	&-logo {
		@include position-center;
		font-family: $ff-special;
		font-size: 20px;
	}

	&-menu {
		width: 40px;

		& button {
			width: 100%;
			border: none;
			background: none;
			@include flex(row, center, center);

			& svg {
				& path:nth-child(3) {
					transform: translate(20%, 0%);
					color: $clr-gray;
				}
			}
		}
	}
}
